import React from "react";
import One from './components/One'
import Two from './components/Two'
import Three from './components/Three'
import Four from './components/Four'
import Five from './components/Five'
import Left from './components/Left'
import srcimg from './assets/quad.jpg'

import ReactWOW from 'react-wow'
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hide: false,
      detail: null
    };
    this.showDetails = this.showDetails.bind(this)
  }

  showDetails(detail = null) {


    this.setState({ hide: !this.state.hide, detail });
    document.body.classList.toggle('scroll-hidden');
  }
  renderHidden() {
    const { detail } = this.state
    switch (detail) {
      case 'one':
        return <One />
      case 'two':
        return <Two />
      case 'three':
        return <Three />
      case 'four':
        return <Four />
      case 'five':
        return <Five />
      default:
        return null
    }
  }

  render() {
    const { hide, detail } = this.state;

    return (
      <div class="wrap">
        <h1 className={hide ? "hide-label absolute-label" : "absolute-label animated fadeIn"}>Omega</h1>
        <div class={hide ? "hide left-side" : "left-side"}>
          <Left hide={hide} />
        </div>
        <div class="right-side">

        <ReactWOW animation="fadeInRight">
          <section className={hide && detail === 'one' ? "fixed" : null} id="one">

            {hide ?
 
              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails();
                }}
              /> :
              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails("one");
                }}
              />
            }
          </section></ReactWOW>
          
          <section className={hide && detail === 'two' ? "fixed" : null} id="two">
            {hide ?

              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails();
                }}
              /> :
              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails("two");
                }}
              />
            }
          </section>
          <section className={hide && detail === 'three' ? "fixed" : null} id="three">
            {hide ?

              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails();
                }}
              /> :
              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails("three");
                }}
              />
            }
          </section>
          <section
            className={hide && detail === 'four' ? "fixed" : null}
            id="four"
          >
            {hide ?

              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails();
                }}
              /> :
              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails("four");
                }}
              />
            }
          </section>
          <section
            className={hide && detail === 'five' ? "fixed" : null}
            id='five'
          >
            {hide ?

              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails();
                }}
              /> :
              <img className="action-img" src={srcimg} alt='quad'

                onClick={() => {
                  this.showDetails("five");
                }}
              />
            }
          </section>
          <section className="locked"></section>
        </div>
        <section
          class={!hide ? "hide hidden-section" : "hidden-section"}>
          <button

            onClick={() => {
              this.showDetails();
            }}
          >
            back to all
</button>
          {this.renderHidden()}
        </section>

      </div>
    );
  }
}

export default App;
